ales se transforman en efectos fisiológicos si esta sigue aumentando; sin embargo, ambos efectos pueden pasar desapercibidos si no se cuenta con técnicas y equipos que permitan ponerlos de manifiesto.")]),_vm._m(13),_c('p',{staticClass:"mb-4"},[_vm._v("En la figura de gradiente de toxicidad, se observa que si no hay exposición (cero dosis) no hay efecto, a medida que se incrementa la dosis pueden empezar a aparecer efectos bioquímicos, los cuales se transforman en efectos fisiológicos si se sigue aumentando la dosis; sin embargo, ambos efectos pueden pasar desapercibidos si no se cuenta con técnicas y equipos que permitan ponerlos de manifiesto.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Cuando la dosis alcanza niveles significativos, que varían de una sustancia a otra, pueden aparecer signos y síntomas clínicos que revelan la producción de efectos adversos que darían lugar a estados patológicos o enfermedades diversas; si la dosis es excesiva puede ocurrir la muerte.")]),_c('p',{staticClass:"mb-5"},[_vm._v("Para ilustrar lo antes expuesto, se puede citar el caso del plomo que, en una cierta dosis, interfiere con la fijación del hierro en la hemoglobina (cambio bioquímico), al elevar la dosis produce anemia (cambio fisiológico) y de seguir incrementándose la exposición puede causar nefropatía o encefalopatía (manifestaciones patológicas), finalmente, si se aumenta más la dosis puede provocar la muerte (Inecc, 2007).")]),_c('hr'),_c('br'),_vm._m(14),_c('p',{staticClass:"mb-4"},[_vm._v("Para los propósitos del manejo seguro de los productos químicos, se debe de tener en cuenta dos clasificaciones de “exposición y efecto”.")]),_vm._m(15),_c('br'),_c('br'),_vm._m(16),_c('hr'),_c('br'),_vm._m(17),_c('br'),_vm._m(18),_c('br'),_c('p',{staticClass:"mb-4"},[_vm._v("Conociendo que las exposiciones crónicas generan efectos adversos sobre los trabajadores, es necesario establecer un límite máximo, que no se pueda sobrepasar a fin de garantizar que la exposición no será nociva para el trabajador. Con este propósito, varias instituciones del mundo hacen investigaciones sobre los límites de exposición a productos químicos. ")]),_c('p',{staticClass:"mb-4"},[_vm._v("La ACGIH es la Conferencia Estadounidense de Higienistas Industriales Gubernamentales y uno de sus objetivos es promover la protección de los trabajadores proporcionando información científica oportuna y objetiva a los profesionales de la salud ocupacional y ambiental. Dentro de sus actividades la ACGIH publica anualmente una relación de los valores permisibles en el ambiente de trabajo conocidos como Valores Límite Umbral (TLV, del inglés Threshold Limit Values) para agentes físicos. Estos valores son solo unos límites recomendables y como tales deben ser interpretados y aplicados. ")]),_vm._m(19),_c('p',{staticClass:"mb-4"},[_vm._v("Los TLV para agentes químicos, expresan concentraciones en aire de diversos productos por debajo de las cuales la mayoría de los trabajadores puede exponerse sin sufrir efectos adversos. Se admite que, dada la variabilidad de respuestas individuales, un porcentaje de trabajadores pueda experimentar ligeras molestias ante ciertos productos en estas concentraciones o por debajo de ellas, incluso en casos raros, puedan verse afectados por agravamiento de dolencias previas o por la aparición de enfermedades profesionales. Debido a los variados efectos que los productos químicos pueden provocar en las personas expuestas, se definen diferentes tipos de valores TLV.")]),_vm._m(20),_vm._m(21),_vm._m(22),_c('hr'),_c('br'),_vm._m(23),_c('p',{staticClass:"mb-4"},[_vm._v("Los efectos colaterales de los productos químicos no se limitan solamente a probables exposiciones a las que se ve enfrentado un trabajador en el momento de usar el producto químico. Los efectos se dan en todos los momentos en los que el producto es activo, es decir, en todo su ciclo de vida.")]),_c('p',{staticClass:"mb-4"},[_vm._v("La idea de “ciclo de vida” se introdujo para evaluar las características ambientales de los productos químicos, y considera cinco etapas básicas: premanufactura, manufactura, envío del producto, uso y fin de su vida útil.")]),_vm._m(24),_c('p',{staticClass:"mb-4"},[_vm._v("Dicha evaluación permite contar con productos con menores impactos ambientales, pero sin que se omitan las características deseadas en la productividad. El reto para los profesionales de la química sería, entonces, el desarrollo de nuevos productos, procesos y servicios que cumplan con los requerimientos sociales, económicos y ambientales.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Para esto, se requiere reducir el consumo de materiales y energía en los procesos, minimizar la emisión al ambiente de productos químicos peligrosos, maximizar el uso de recursos renovables y extender la durabilidad y el reciclaje de los productos (Global, 2011).")]),_vm._m(25),_c('br'),_c('div',{staticClass:"row mb-4 color-primario py-5"},[_vm._m(26),_c('div',{staticClass:"col-7 d-flex align-items-center justify-content-center"},[_c('div',{staticClass:"div"},[_vm._m(27),_c('a',{staticClass:"w-80 anexo mb-4",attrs:{"href":_vm.obtenerLink('/downloads/Anexo_1_Contaminantes_orgánicos_persitentes_COP.pdf'),"target":"_blank"}},[_vm._m(28),_vm._m(29)])])])]),_c('div',{staticClass:"mb-4"},[_vm._v("Existe otro grupo de contaminantes ambientales que pueden ser tomados del agua o de los alimentos y son retenidos por la fauna, la flora o los seres humanos en sus cuerpos y acumularse en concentraciones mayores que las contenidas en sus alimentos y el agua. Se dice, entonces, que estos productos químicos son bioacumulativos y pueden causar efectos perjudiciales cuando alcanzan niveles suficientemente elevados. ")]),_c('br'),_vm._m(30),_c('p',{staticClass:"mb-4"},[_vm._v("Sin embargo, no todas las afecciones de los productos químicos se producen directamente en la fauna, la flora o los seres humanos, otros pueden ocasionar cambios en el medio ambiente y representan peligros. Algunos ejemplos de ello son los productos químicos orgánicos volátiles y los óxidos de nitrógeno, los cuales dan lugar al ozono troposférico (o “smog”), y los clorofluorocarbonos (CFC), que degradan las capas de ozono estratosféricas y hacen que una mayor radiación ultravioleta incida sobre la superficie de la Tierra.")]),_vm._m(31),_c('p',{staticClass:"mb-5"},[_vm._v("Finalmente, también existen algunos contaminantes ambientales en cuyo caso la ciencia no ha podido demostrar su nivel de “no efecto” sobre los seres humanos (por ejemplo, partículas de sulfato en el aire, niveles de plomo en la sangre de niños) (Pnuma, 2013). ")])])],1)}
